import React, { Component } from 'react';
import { useTranslation, withTranslation, Trans } from 'react-i18next';
import './Hero.css';


class Hero extends Component {
  render() {
    const { t, i18n } = this.props;
    return (
      <div className="Hero">
        <div className="Hero-mobile-container">
          <img className="Hero-marco" src={this.props.marcoMobile} alt={"marcoHero"}></img>
          <img className="Hero-marco-desktop" src={this.props.marco1Desktop} alt={"marcoHero"}></img>
          <img className="Hero-marco2-desktop" src={this.props.marco2Desktop} alt={"marcoHero"}></img>
          <img className="Hero-lineas" src={this.props.lineas} alt={"marcoHero"}></img>
          <img className="Hero-photo" src={this.props.hero.photo} alt={"photoHero"}></img>
          <p className="Hero-name">{this.props.hero.name}
            <img className="Hero-marco-text" src={this.props.marcoText} alt={"marcoTextHero"}></img>
          </p>
        </div>
          <p className="Hero-description">{t(this.props.hero.description)}</p>
      </div>
    );
  }
}

export default withTranslation()(Hero);
